.app__specialMenu{
    flex-direction: column;
    background-color: var(--color-black);
}

.app__specialMenu-title{
    margin-bottom: 2rem;
    text-align: center;
}

.app__specialMenu-menu{
    width: 100%;
    margin: 2rem 0;
    display: flex;
    justify-content:center;
    flex-direction: row;
    align-items: flex-start;
}

.app__specialMenu-menu_heading{
    font-family: var(--font-base);
    font-weight: 600;
    font-size: 45px;
    line-height: 58.5px;
    letter-spacing: 0.04rem;
    color: var(--color-white);
}

.app__specialMenu-menu_wines, .app__specialMenu-menu_cocktails{
    width: 100%;
    flex-direction: column;
    flex: 1;
}

.app__specialMenu-menu_img{
    width: 410px;
    margin: 0 2rem;
}

.app__specialMenu-menu_img img{
    width: 100%;
    height: auto;
}

.app__specialMenu_menu_item{
    display: flex;
    flex-direction: column;

    margin: 2rem 0;
    width: 100%;
}

@media screen and (max-width: 2000px){
    .app__specialMenu-menu_img{
        width: 650px;
    }
    .app__specialMenu-menu_img img{
        height: auto;
    }
}

@media screen and (max-width: 1150px){
    .app__specialMenu-menu{
        flex-direction: column;
        align-items: center;
        width: 100%;
    }
    .app__specialMenu-menu_img{
        margin: 3rem 0;
    }

}

@media screen and (max-width: 650px){
    .app__specialMenu-menu_img{
        width: 100%;
    }

}
    
