.app__chef-content{
    display: flex;
    flex-direction: column;
    
    width: 100%;
    margin-top: 5rem;
}

.app__chef-content_quote{
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
}

.app__chef-content_quote img{
    width: 50px;
    height: 40px;
    margin: 0 1rem 1rem 0;
}

.app__chef-sign{
    width: 100%;
    margin-top: 3rem;
}
.app__chef-sign p:first-child{
    color: var(--color-golden);
    font-family: var(--font-base);
    font-weight: 400;
    font-size: 40px;
    line-height: 41px;
    letter-spacing: 0.04em;
    text-transform: capitalize;
}

.app__chef-sign img{
    width: 250px;
    margin-top: 1rem;
}

@media screen and (max-width: 2000px) {

    .app__chef{
        margin-top: 3rem;
    }

    .app__chef-sign img{
        width: 300px;
    }
}

@media screen and (max-width: 650px) {
    .app__chef-content{
        margin-top: 2rem;
    }
    .app__chef-sign img{
        width: 80%;
    }
}

