.app__footer{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--color-black);
    z-index: 1;
    position: relative;
    padding-top: 0;
}

.app__footer-links{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: 5rem;
    padding: 0 2rem;
}

.app__footer-links_contact,
.app__footer-links_logo,
.app__footer-links_work{
    flex: 1;
    margin: 1rem;
    text-align: center;
}

.app__footer-links_logo img:first-child{
    width: 200px;
    margin-bottom: 1rem;
}

.app__footer-links_icons svg{
    color: var(--color-white);
    margin: 1.5rem 1rem;
    cursor: pointer;
    font-size: 20px;
    transition: 0.5s ease;
}

.app__footer-links_icons svg:hover{
    color: var(--color-golden);
    font-size: 35px;
}

.app__footer-links_work p{
    letter-spacing: 0.2rem;
}
.app__footer-links_work p:nth-child(2n+1){
    margin-bottom: 1rem;
}
.app__footer-headtext{
    font-family: var(--font-base);
    color: var(--color-white);
    margin-bottom: 2rem;
    font-weight: 400;
    letter-spacing: 0.04em;
    text-transform: capitalize;
    font-size: 32px;
    line-height: 40px;
}

.footer_copyright{
    margin-top: 3rem;
}

@media screen and (min-width: 2000px){
    .app__footer-headtext{
        font-size: 50px;
        line-height: 60px;
    }
}

@media screen and (max-width: 1150px){
    .app__footer-links{
        flex-direction: column;
        align-items: center;
    }

    .app__footer-links_contact,
    .app__footer-links_logo,
    .app__footer-links_work{
        margin: 2rem 0;
        width: 100%;
    }


}

@media screen and (max-width: 650px){
    .app__footer{
        padding: 0 0 2rem 0;
    }
}

@media screen and (max-width: 350px){
    .app__footer-links_logo{
        width: 80%;
    }
}